<template>
  <v-container
    fluid
    grid-list-md>
    <v-layout
      row
      wrap>
      <v-flex md12>
        <material-card
          color="primary"
          title="Pesquisar Loja">
          <v-card class="elevation-0">
            <v-card-text>
              <v-form>
                <v-layout
                  row
                  wrap>
                  <v-flex md1>
                    <v-text-field
                      v-model="filtros.id"
                      label="Código"
                    />
                  </v-flex>
                  <v-flex md4>
                    <v-text-field
                      v-model="filtros.razaoSocial"
                      label="Razão Social"
                    />
                  </v-flex>
                  <v-flex md4>
                    <v-text-field
                      v-model="filtros.nomeFantasia"
                      label="Nome Fantasia"
                    />
                  </v-flex>
                  <v-flex md3>
                    <v-text-field
                      v-mask="'##.###.###/####-##'"
                      v-model.trim="filtros.cnpj"
                      label="CNPJ"
                    />
                  </v-flex>
                  <v-flex md3>
                    <v-autocomplete
                      v-model="filtros.status"
                      :items="listaStatus"
                      label="Status"
                      clearable
                    />
                  </v-flex>
                  <v-flex md3>
                    <v-autocomplete
                      v-model="filtros.ramoAtividadeId"
                      :items="ramosAtividade"
                      label="Ramo Atividade"
                      item-value="id"
                      item-text="descricao"
                      clearable
                    />
                  </v-flex>
                </v-layout>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer />
              <v-btn
                :loading="loadingBtn"
                color="primary"
                @click="pesquisar"
              >Pesquisar</v-btn>
            </v-card-actions>
          </v-card>
        </material-card>
      </v-flex>
      <v-flex md12>
        <v-card>
          <v-toolbar
            flat
            color="white">
            <v-toolbar-title>Lojas</v-toolbar-title>
            <v-divider
              class="mx-2"
              inset
              vertical
            />
            <v-spacer />
            <v-btn
              small
              fab
              color="primary">
              <v-icon @click="newItem()">mdi-plus</v-icon>
            </v-btn>
          </v-toolbar>
          <v-progress-linear
            :active="loading"
            color="light-blue"
            indeterminate
            height="3"
          />
          <v-data-table
            :headers="headers"
            :items="lojas"
            :total-items="totalItems"
            :pagination.sync="pagination"
            class="elevation-1 arrow-pagintion"
          >
            <template
              slot="headerCell"
              slot-scope="{ header }">
              <span
                class="subheading font-weight-light text--darken-3"
                v-text="header.text" />
            </template>
            <template
              slot="items"
              slot-scope="{ item }"
              ma-5>
              <td>{{ item.razaoSocial }}</td>
              <td>{{ item.nomeFantasia }}</td>
              <td>{{ item.cnpj }}</td>
              <td>
                <v-chip
                  v-if="item.status === 'ATIVO'"
                  color="green"
                  class="ml-0"
                  text-color="white"
                >
                  <v-avatar class="green darken-4">A</v-avatar>
                  {{ item.status }}
                </v-chip>

                <v-chip
                  v-if="item.status === 'BLOQUEADO'"
                  color="red"
                  class="ml-0"
                  text-color="white"
                >
                  <v-avatar class="red darken-4">B</v-avatar>
                  {{ item.status }}
                </v-chip>
                <v-chip
                  v-if="item.status === 'CANCELADO'"
                  color="orange"
                  class="ml-0"
                  text-color="white"
                >
                  <v-avatar class="orange darken-4">C</v-avatar>
                  {{ item.status }}
                </v-chip>
              </td>
              <td>{{ item.empresaLabel }}</td>
              <td>{{ item.ramoAtividadeLabel }}</td>
              <td>{{ item.chaveTEF }}</td>
              <td class="justify-end layout ma-2">
                <v-icon
                  class="mr-2"
                  color="blue"
                  title="Editar dados da loja"
                  @click="editItem(item)"
                >mdi-pencil</v-icon>
                <v-icon
                  color="red"
                  title="Excluir loja"
                  @click="openDialogDelete(item)"
                >mdi-delete</v-icon>
              </td>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
      <template
        v-if="dialog"
      >
        <v-layout
          row
          justify-center>
          <v-dialog
            v-model="dialog"
            max-width="390">
            <v-card>
              <v-card-title class="headline">Excluir Loja</v-card-title>

              <v-card-text>Confirma a exclusão da Loja <span class="title">{{ item.razaoSocial }}</span>? Essa operação não poderá ser desfeita.</v-card-text>

              <v-card-actions>
                <v-spacer />

                <v-btn
                  outline
                  style="color: black !important"
                  @click="closeDialogDelete"
                >Cancelar</v-btn>

                <v-btn
                  color="red"
                  @click="deleteItem"
                >Excluir</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </template>
    </v-layout>
  </v-container>
</template>

<script>
import { LojaBusiness, TiposBusiness } from '../../business'

export default {
  metaInfo: {
    titleTemplate: '%s - Lojas'
  },
  data () {
    return {
      dialog: false,
      item: null,
      loading: false,
      totalItems: 0,
      pagination: {
        rowsPerPage: 10,
        sortBy: 'id'
      },
      filtros: {
        id: null,
        razaoSocial: null,
        nomeFantasia: null,
        cnpj: null,
        status: null,
        ramoAtividadeId: null,
        empresaId: null
      },
      lojas: [],
      ramosAtividade: [],
      headers: [
        {
          text: 'Razão Social',
          value: 'razaoSocial',
          sortable: false
        },
        {
          text: 'Nome Fantasia',
          value: 'nmFantasia',
          sortable: false
        },
        {
          text: 'CNPJ',
          value: 'cnpj',
          sortable: false
        },
        {
          text: 'Status',
          value: 'status',
          sortable: false
        },
        {
          sortable: false,
          text: 'Empresa',
          value: 'empresaId'
        },
        {
          sortable: false,
          text: 'Ramo Atividade',
          value: 'ramoAtividade'
        },
        {
          sortable: false,
          text: 'Chave TEF',
          value: 'chaveTEF'
        },
        {
          sortable: false,
          text: ''
        }
      ],
      listaStatus: [],
      loadingBtn: false
    }
  },
  watch: {
    pagination: {
      handler () {
        this.paginar()
      }
    }
  },
  mounted () {
    this.reload()
  },
  methods: {
    reload () {
      if (this.listaStatus.length <= 0) {
        TiposBusiness.listStatus()
          .then(response => { this.listaStatus = response.data })
          .catch(() => this.$root.showErro('Não foi possível buscar as informações da lista de status'))
      }

      if (this.ramosAtividade.length <= 0) {
        TiposBusiness.listRamoAtividade()
          .then(response => { this.ramosAtividade = response.data })
          .catch(() => this.$root.showErro('Não foi possível buscar as informações da lista de ramo atividade'))
      }
    },
    newItem () {
      this.$router.push('cad-loja')
    },
    editItem (item) {
      this.$router.push(`cad-loja/${item.id}`)
    },
    openDialogDelete (item) {
      this.item = item
      this.dialog = true
    },
    closeDialogDelete () {
      this.item = null
      this.dialog = false
    },
    deleteItem () {
      LojaBusiness.delete(this.item.id)
        .then(response => {
          this.$root.showSucesso(response.data)
          this.closeDialogDelete()
          this.paginar()
        })
        .catch(error => this.$root.showErro(error.data))
    },
    pesquisar () {
      this.loadingBtn = true
      this.paginar()
    },
    paginar () {
      if (this.$root.isProprietario()) {
        this.filtros.empresaId = this.$root.empresaId()
      }
      this.loading = true
      LojaBusiness.pagination(this.pagination.rowsPerPage, this.pagination.page - 1, this.pagination.sortBy || 'id', this.filtros)
        .then(response => {
          this.lojas = response.data.content
          this.totalItems = response.data.totalElements
        })
        .finally(() => {
          this.loadingBtn = false
          this.loading = false
        })

      this.reload()
    }
  }
}
</script>
